<template>
	<view class="content">
		<view class="header">
			<view class="title">我的一天</view>
			<p>{{dayjs(new Date()).format('YYYY年MM月DD日')}}</p>
		</view>

		<view class="inputItem">
			<input type="text" v-model="toDoItem" @keydown.enter="handleEnter" />
		</view>

		<scroll-view scroll-y="true">

			<view class="todolist">
				<view class="item" v-for="(item,index) in 5">
					清单{{index}}
				</view>
			</view>
		</scroll-view>

		<view class="add">
			<uni-icons type="plusempty" size="30" color="#fff" @click="addItem"></uni-icons>
			<uni-popup ref="popup" type="bottom" border-radius="10px 10px 0 0">底部弹出 Popup 自定义圆角</uni-popup>
		</view>
	</view>




</template>

<script setup>
	import {
		ref
	} from 'vue';
	import dayjs from 'dayjs'
	const popup = ref(null)
	const toDoItem = ref('')
	const handleEnter = () => {
		console.log(toDoItem.value)
		toDoItem.value = ''
	}

	const addItem = () => {
		popup.value.open('top')
	}
</script>

<style scoped>
	.content {
		width: 100vw;
		height: 100vh;
		margin: 0;
		padding: 20rpx;
		font-size: 50rpx;
		background: url('/static/background.jpg') repeat center/cover fixed;

		.header {
			color: #fff;

			.title {
				font-size: 50rpx;
				margin-bottom: 5rpx;
			}

			p {
				font-size: 35rpx;
			}
		}

		.inputItem {
			padding: 20rpx 0;

			input {
				background-color: #fff
			}
		}

		.todolist {
			width: 100%;
			height: 100%;

			.item {
				padding: 20rpx;
				border-radius: 20rpx;
				margin-bottom: 20rpx;
				background-color: aliceblue;
			}
		}

		.add {
			background-color: #297f8b;
			border-radius: 60rpx;
			padding: 30rpx;
			position: fixed;
			bottom: 50rpx;
			right: 50rpx;
		}
	}
</style>